#root{color:black;background-color:white}#layout-wrapper{position:relative;display:flex;flex-direction:column}#layout-header{display:flex;flex-direction:row;min-height:10vh;border-bottom:1px solid;align-items:center}#layout-header div{width:33.33%}#layout-header:first-child{text-align:center}ul#layout-footer{display:flex;flex-direction:row;min-height:10vh;list-style-type:none;justify-content:space-around;width:100%;border-top:1px solid;margin:0px;padding:0px}ul#layout-footer li{width:25%}ul#layout-footer li a{text-decoration:none;padding:5px;border-right:1px solid;display:inline-block;width:100%;height:100%;box-sizing:border-box;text-align:center}#main-content{min-height:80vh}
